import React, { useState } from "react";
import {NavLink} from 'react-router-dom';
import './Header.scss';
import Nav from '../Nav/Nav.js';

const Header = () => {
    const [navActive, setNavActive] = useState(false);

    const showMenu = () => {
        (navActive === false) ? setNavActive(true) : setNavActive(false);
    };

    const hideMenu = () => {
        setNavActive(false);
    };

    return (
        <header className={ (navActive) ? "header active" : "header" }>
            <NavLink exact to="/" className="logo" activeClassName={'current-page'}> Новостник </NavLink>
            <Nav activeState={ navActive } changeState={ showMenu } hide={ hideMenu } />
        </header>
    );
}

export default Header;