import React from "react";
import {NavLink} from 'react-router-dom';

const NavItem = props => {

    return (
        <li>
            <NavLink 
                exact 
                to={ props.path }
                activeClassName={'current-page'}
                onClick={ () => props.hideMenu() }
            > 
                { props.text } 
            </NavLink>
        </li>
    );
}

export default NavItem;