import React from "react";
import {NavLink} from 'react-router-dom';
import './Footer.scss';

const Footer = () => (
    <footer className="footer">
        <div className="footer__wrap">
            <NavLink exact to="/" className="logo" activeClassName={'current-page'}> 
                Новостник 
                <span> Single Page Application </span>
            </NavLink>
            <p className="footer-text"> Дипломный проект </p>
            <div className="made-by">
                <span> Made by </span>
                <p> Сергей Тормозов </p>
            </div>
        </div>
    </footer>
);

export default Footer;